#searchIcon{
    cursor: pointer;
    margin-right: 0.5rem;
    margin-top: 0.3rem;
}

#search-bar{
    width: 70vh;
    border: none;
    background:#ededf3;
    border-radius: 15px;
    text-decoration: none;
    padding-top: 0.2rem;
    padding-bottom: 0.2rem;
}

textarea, input{
    outline: none;
    padding-left: 1rem;
}

#search-bar:hover{
    background: #dfdfe6;
}

.Search-Bar{
    margin-left: 1rem;
    margin-top: 1rem;
}
.addText{
    display:inline-flex;
    position: absolute;
    top:1.3rem;
    left:68vh;
}

.Search-expand{
    display:inline-flex;
    position: absolute;
    top:1.3rem;
    left:57vh;
    background: #525387;
    height:20px;
    color: white;
    padding: 0;
}